html {
  font-family: helvetica
}

body {
margin: 0
}

p {
  text-align: center;
}

.nav {
  display: flex;
  justify-content: space-between;
  width: 750px;
  margin: auto
}

.nav-div ul {
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
  padding: 0;
}

.nav-div ul li {
  padding-right: 25px;
}

li {
  list-style: none;
}

.title-div span {
  padding: 25px;
  display: block;
}

.title-div {
  height: 100%;
}

ul {
  margin: 0
}

.footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
}